import {
  JsonML,
} from '@ali/4ever-utils';
import { MoState, MoAttributes, createTextWithString } from '@ali/4ever-cangjie';

export default {
  name: 'embed',

  onOpenTag(state: MoState, name: string, attrs: MoAttributes): boolean {
    if (name !== 'video') return false;

    const rawAttrs: Record<string, string | number | Record<string, string | number>> = {
      src: attrs.src || '',
      name: attrs.title ? attrs.title : '',
      type: 'video',
      mimeType: '',
      size: Number(attrs['data-size']) || 0,
      viewType: 'preview',
      previewSize: {
        height: 300,
      },
    };

    const embed: JsonML = ['embed', rawAttrs, createTextWithString('')] as JsonML;
    state.push(embed);
    return true;
  },

  onCloseTag(state: MoState, name: string): boolean {
    if (name !== 'video') return false;

    const video = state.pop();
    state.append(video);
    return true;
  },
};
